<template>
  <div class="page-collection">
    <div class="collheader">
      <div class="collheader_left">
        <van-icon @click="$router.go(-1)"
                  name="https://s1.ax1x.com/2020/03/28/Gk5Bse.png"
                  size="0.4rem" />
        <span>{{name}}</span>
      </div>
      <div class="collheader_right">
        <transition name="fade">
          <van-icon name="https://s1.ax1x.com/2020/03/28/GFyZG9.png"
                    size="0.4rem"
                    ref="searchIcon"
                    @click="searchByName()" />
        </transition>
        <input type="text"
               v-model="searchValue"
               v-show="searchLoad"
               @change="filterSong">
        <van-icon name="https://s1.ax1x.com/2020/03/28/GFsgHO.png"
                  size="0.4rem" />
      </div>
    </div>
    <div class="collbcg"
         :style="{'background-image': `url(${colldata.coverImgUrl})`}">
    </div>
    <div class="collab">
      <div class="collbcg_info">
        <div class="collbcg_infoleft">
          <img :src="colldata.coverImgUrl"
               alt="">
        </div>
        <div class="collbcg_inforight">
          <div class="collbcg_title">
            {{colldata.name}}
          </div>
          <div class="coll_user">
            <img :src="colldata.creator.avatarUrl"
                 alt=""><span>{{colldata.creator.nickname}}</span>
            <van-icon name="https://s1.ax1x.com/2020/03/28/GFykaF.png"
                      size="12" />
          </div>
          <div class="coll_content">
            {{colldata.description}}
          </div>
        </div>

      </div>
      <div class="grid_collcard">
        <van-grid :column-num="4"
                  :border="false">
          <van-grid-item icon="https://s1.ax1x.com/2020/03/28/GFsRED.png"
                         :text="colldata.commentCount" />
          <van-grid-item icon="https://s1.ax1x.com/2020/03/28/GFs58A.png"
                         :text="colldata.shareCount" />
          <van-grid-item icon="https://s1.ax1x.com/2020/03/28/GFsf4H.png"
                         text="下载" />
          <van-grid-item icon="https://s1.ax1x.com/2020/03/28/GFsovt.png"
                         text="多选" />
        </van-grid>
      </div>
    </div>
    <!-- 歌曲列表 -->
    <div class="songlist">
      <div class="songlist_header">
        <div class="songlist_headerleft"
             @click="playAll">
          <van-icon name="https://s1.ax1x.com/2020/03/26/GphbSs.png"
                    size="0.4rem" />
          <h5>播放全部</h5><span>(共{{colldata.tracks.length}}首)</span>
        </div>
        <div class="songlist_headerright">
          <van-button round
                      color="#dd101b">
            <span style="margin-right:5px">+</span>收藏({{(colldata.subscribedCount/10000)>=1?
                      parseInt((colldata.subscribedCount/10000)).toFixed(1)+'万':colldata.subscribedCount}})
          </van-button>
        </div>
      </div>
      <!-- list -->
      <div class="songlist_items">
        <div :key="index"
             @click="handleSong(item,index,item.id)"
             class="songlist_item"
             v-for="(item, index) in filterSong()">
          <div class="song_itemindex">
            {{index+1}}
          </div>
          <div class="songlist_itemright">
            <div class="songlist_right_left"
                 style="line-height:20px">
              <div class="songlist_left_name suojin">{{item.name}}</div>
              <div class="songlist_author suojin">
                <span class="songlist_sq"
                      v-if="item.fee==1"
                      :class="{'sprite_vip':item.fee==1,}"></span>
                <span class="songlist_sq"
                      :class="{'sprite3': item.fee==4 ||item.copyright==2 ||item.copyright==0 ||item.fee==1}"
                      v-if="item.fee==4 ||item.copyright==2 ||item.copyright==0 ||item.fee==1"></span>
                {{item.ar.map(v=>v.name).join('/')}}
                -{{item.al.name}}
              </div>
            </div>
            <div class="songlist_right_right">
              <van-icon name="https://s1.ax1x.com/2020/03/26/GphbSs.png"
                        size="0.4rem" />
              <van-icon name="https://s1.ax1x.com/2020/03/27/GibKPI.png"
                        size="0.6rem" />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props:
  {
    name: { type: String },
    colldata: {
      avatarUrl: {}
    },
  },
  data () {
    return {
      songurl: '',
      searchLoad: false,
      searchValue: '',
      // 是否有版权
    }
  },
  methods: {
    handleSong (item, index, songid, singer, songname) {
      // console.log(item);
      if (item.fee == 1)      {
        return this.$toast('vip歌曲暂时无法播放')
      } else if (item.fee == 4)      {
        return this.$toast('请先购买专辑')
      }
      // 判断歌曲是否有版权
      this.$http.get(`/check/music?id=${item.id}`).then(res => {
        if (this.$store.state.playlist)        {
          this.$store.commit("addSong", { item: item.al, index: index, songid: songid, singer: item.ar.map(v => v.name).join('/'), songname: item.name })
          this.$store.commit('pushSong', item.al)
          this.fetchSongurl(item.id)
        } else        {
          this.$store.commit('changeFull')
          this.$store.commit("addSong", { item: item.al, index: index, songid: songid, singer: item.ar.map(v => v.name).join('/'), songname: item.name })
          this.$store.commit('pushSong', item.al)
          this.fetchSongurl(item.id)
        }
      }, err => {
        this.hasCopy = false
        return this.$toast('亲爱的,暂无版权')
      })
    },
    async  fetchSongurl (id) {
      const res = await this.$http.get(`song/url?id=${id}`)
      this.songurl = res.data.data[0].url
      this.$store.commit('addurl', this.songurl)
    },
    // 点击显示搜索框
    searchByName () {
      this.$refs.searchIcon.style.display = 'none'
      this.searchLoad = true
    },
    // 检索
    filterSong () {
      return this.colldata.tracks.filter(item => item.name.includes(this.searchValue))
    },
    playAll () {
      this.colldata.tracks.map(async v => {
        const res = await this.$http(`song/url?id=${v.id}`)
        v.al.songname = v.name
        v.al.songid = v.id
        v.al.singer = v.ar.map(a => a.name).join('/')
        v.al.url = res.data.data[0].url
      })
      this.$store.commit('pushAllSong', this.colldata.tracks)
    }
  },
}
</script>

<style>
.suojin {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.page-collection {
  position: relative;
}
.collheader {
  position: relative;
  z-index: 999;
  box-sizing: border-box;
  height: 1rem;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 0 0.4rem;
  justify-content: space-between;
}
.collheader_left span {
  color: #fff;
  font-size: 0.32rem;
}
.collheader_right {
  justify-content: space-between;
  width: auto;
  display: flex;
  align-items: center;
}
.collheader_right .van-icon {
  margin-left: 0.4rem;
}
.collheader_right input[type="text"] {
  border-radius: 0.4rem;
  border: none;
  outline: none;
  padding: 0.1rem 0.3rem;
  background-color: rgba(0, 0, 0, 0.5);
  font-size: 0.24rem;
  color: #fff;
}
.collbcg {
  top: 0rem;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  height: 7rem;
  width: 100%;
  position: absolute;
  background-size: cover;
  background-position: center;
}
.collbcg::before {
  content: " ";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: 800;
  background-size: 120%;
  background-color: rgba(0, 0, 0, 0.5);
}
.collbcg::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  filter: blur(0.3rem);
  background: inherit;
  background-size: 120%;
  z-index: 100;
  opacity: 1;
  top: 0;
}
.collab {
  box-sizing: border-box;
  width: 100%;
  padding: 0 0.2rem;
  position: absolute;
  z-index: 999;
}
.collbcg_info {
  height: 3.2rem;
  display: flex;
  margin-top: 0.4rem;
  /* align-items: center; */
}
.collbcg_infoleft img {
  width: 3rem;
  border-radius: 0.1rem;
}
.collheader_left {
  height: 100%;
  display: flex;
  align-items: center;
}
.collheader_left span {
  margin-left: 0.4rem;
}

.collab .van-grid-item__content {
  background-color: transparent;
}
.collab .van-grid-item__text {
  color: #fff;
}
.collbcg_inforight {
  margin-left: 0.2rem;
}
.collbcg_title {
  color: #fff;
  height: 1rem;
  text-overflow: ellipsis;
  overflow: hidden;
  font-weight: bold;
  font-size: 0.36rem;
}
.coll_user {
  display: flex;
  align-items: center;
  margin-top: 0.2rem;
}
.coll_user img {
  width: 0.4rem;
  border-radius: 50%;
}
.coll_user span {
  margin-left: 0.2rem;
  font-size: 0.26rem;
  color: #fff;
}
.coll_content {
  height: 0.66rem;
  overflow: hidden;
  margin-top: 0.4rem;
  color: #fff;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  text-overflow: ellipsis;
  font-size: 0.24rem;
}
/*  */
.songlist {
  position: relative;
  box-sizing: border-box;
  top: 5.2rem;
  z-index: 999;
  margin-bottom: 1rem;
}
.songlist_right_right {
  display: flex;
  align-items: center;
}
.songlist_header {
  height: auto;
  box-sizing: border-box;
  display: flex;
  width: 100%;
  align-items: center;
  padding: 0.1rem 0.2rem 0.2rem 0.2rem;
  justify-content: space-between;
  height: 1.4rem;
  background-color: #fff;
  border-top-left-radius: 0.42rem;
  border-top-right-radius: 0.42rem;
  /* z-index: 999; */
}
.songlist_headerleft {
  display: flex;
  align-items: center;
}
.songlist_headerleft h5 {
  margin-left: 0.2rem;
  font-size: 0.3rem;
  font-weight: 500;
}
.songlist_headerleft span {
  color: #797979;
  font-size: 0.24rem;
}
/*  */

.sprite3 {
  background: url("//s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28…=")
    no-repeat 0 0;
  background-size: 3.32rem 1.94rem;
  width: 0.24rem;
  height: 0.16rem;
}
.songlist_items {
  box-sizing: border-box;
  background-color: #fff;
  padding: 0 0.2rem;
}
.songlist_item {
  display: flex;
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
}
.song_itemindex {
  font-style: italic;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #797979;
  width: 1.1rem;
  height: 1.1rem;
  font-size: 0.32rem;
}
.songlist_itemright {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex: 1;
  width: 50%;
  /* height: 100%; */
  box-sizing: border-box;
}
.songlist_right_left {
  width: 70%;
  height: 0.4rem;
  line-height: 0.4rem;
}
.songlist_left_name {
  font-size: 0.28rem;
  box-sizing: border-box;
  height: 0.28rem;
  line-height: 0.28rem;
  margin-bottom: 0.2rem;
}
.songlist_author {
  font-size: 0.2rem;
  color: #797979;
}
.songlist_sq {
  display: inline-block;
}
/* 离开动画 */
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.sprite_vip {
  background: url(https://s1.ax1x.com/2020/04/11/GbXi9K.png) no-repeat 0 0;
  background-size: 0.86rem 11.08rem;
  width: 0.12rem;
  height: 0.12rem;
}
.hasCopy {
  color: #797979;
}
.songlist_headerright {
  display: flex;
  align-items: center;
}
.van-tab__text {
  font-size: 0.32rem;
}
.square_tab {
  margin-bottom: 0.2rem;
}
.grid_collcard{
  /* margin-top: 0.2rem; */
}
.grid_collcard .van-grid-item__text {
  font-size: 0.24rem;
}
</style>


