<template>
  <div class="radiopay_pages">
    <div class="radiopay_content">
      <div class="radiopay_header">
        <div>
          <p><span>精品推荐</span><span>你值得拥有的优质内容</span></p>
        </div>
        <div class="radiopay_alljp"><button>全部精品</button></div>
      </div>
      <!--  -->
      <div class="radiopay_desc">
        <div class="radiopay_item"
             v-for="(item, index) in jp"
             :key="index">
          <div class="radio_itemimgalt">
            <div class="radio_itemimg">
              <img v-lazy="item.picUrl"
                   alt="">
              <div class="radiopay_descname">
                {{item.name}}
              </div>
            </div>
            <div class="radiopay_rice">
              <p>{{item.rcmdText}}</p>
              <p><em>￥&nbsp;{{item.originalPrice|fliterPrice}}</em></p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      jp: []
    }
  },
  // 过滤价格
  filters: {
    fliterPrice (val) {
      let price = val / 100
      return price
    }
  },
  created () {
    this.fetchJp()
  },
  methods: {
    async fetchJp () {
      const res = await this.$http.get('dj/paygift')
      this.jp = res.data.data.list.slice(0, 3)
    }
  },
}
</script>

<style>
.radiopay_header {
  padding: 0 0.2rem;
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}
.radiopay_header p {
  margin: 0;
}
.radiopay_header p span:nth-child(2) {
  margin-left: 0.2rem;
  font-size: 0.24rem;
  color: #797979;
}
.radiopay_header p span:nth-child(1) {
  font-size: 0.48rem;
}
.radiopay_alljp button {
  outline: none;
  border: 0.02rem solid #797979;
  background-color: transparent;
  font-size: 0.24rem;
  padding: 0.06rem 0.2rem;
  /* letter-spacing: 0.1rem; */
  border-radius: 0.4rem;
}
/*  */
.radiopay_content {
  background-image: linear-gradient(#f5cd79, #fff);
  padding: 0.4rem 0;
}
.radiopay_desc {
  display: flex;
}
.radiopay_item {
  display: flex;
  flex-direction: column;
  padding: 0.2rem 0.1rem;
  width: 33.3%;
}
.radiopay_desc {
  padding: 0 0.1rem;
}
.radio_itemimg img {
  width: 100%;
  border-radius: 0.1rem;
  /* height: 2.24rem; */
  /* height: 100%; */
  object-fit: cover;
}
.radio_itemimg {
  position: relative;
  height: 80%;
  /* width: 2.24rem; */
}
/* .radio_itemimg::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2.24rem;
  background-color: rgba(42, 42, 42, 0.69);
  opacity: 0.5;
  left: 0;
  top: 0%;
  bottom: 0;
  border-radius: 0.1rem;
} */
.radio_itemimg::after {
  content: "付费精品";
  position: absolute;
  top: 0;
  /* width: 50px; */
  color: #fff;
  padding: 0.1rem;
  border-radius: 0.1rem 0 0.1rem 0;
  left: -0.12rem;
  top: -0.06rem;
  font-size: 0.2rem;
  transform: scale(0.8);
  background-color: #dd101b;
  /* letter-spacing: 5px; */
}
.radiopay_descname {
  position: absolute;
  bottom: 0.3rem;
  left: 0.16rem;
  font-size: 0.24rem;
  color: #fff;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 90%;
}
.radiopay_pages {
  /* margin-bottom: 150px; */
  width: 100%;
  margin-top: 0.2rem;
}
.radio_itemimgalt {
  display: flex;
  flex-direction: column;
}
.radio_itemimgalt p {
  font-size: 0.24rem;
  margin: 0;
}

.radiopay_rice {
}
.radiopay_rice p:nth-child(2) {
  font-size: 0.24rem;
  margin: 0;
  color: #dd101b;
}
</style>


