<template>
  <div class="radio_pages">
    <div class="radio_main"
         v-if="!hasLoad">
      <van-sticky>
        <div class="radiohome_header">
          <van-icon name="arrow-left"
                    size="0.4rem"
                    @click="$router.go(-1)" />
          <span>电台</span>
        </div>
      </van-sticky>
      <radiobanner></radiobanner>
      <!-- 宫格 -->
      <div class="grid_radio">
        <van-grid :border="false">
          <van-grid-item icon="https://s1.ax1x.com/2020/04/24/J0fO2R.png"
                         text="电台分类" />
          <van-grid-item icon="https://s1.ax1x.com/2020/04/24/J0hFGd.png"
                         text="电台排行" />
          <van-grid-item icon="https://s1.ax1x.com/2020/04/24/J0hAxI.png"
                         text="付费精品" />
          <van-grid-item icon="https://s1.ax1x.com/2020/04/24/J0hKIg.png"
                         text="主播学院" />
        </van-grid>
      </div>
      <!-- 听听 -->
      <radiohear></radiohear>
      <!-- 付费精品 -->
      <radiopay></radiopay>
      <!-- 创作|翻唱 -->
      <radiocolumn :data="allreco.creation"></radiocolumn>
      <!-- 有声书 -->
      <radiorow :data="allreco.audiobook"></radiorow>
      <!-- 情感调频 -->
      <radiocolumn :data="allreco.emotion"></radiocolumn>
      <!-- 广播剧 -->
      <radiorow :data="allreco.radioplay"></radiorow>
      <!-- 音乐故事 -->
      <radiocolumn :data="allreco.story"></radiocolumn>
      <!-- 影视|娱乐 -->
      <radiorow :data="allreco.movies"></radiorow>
      <!-- 音乐电子 -->
      <radiocolumn :data="allreco.d3"></radiocolumn>
      <!-- 美文读物 -->
      <radiorow :data="allreco.lettres"></radiorow>
      <!-- 二次元 -->
      <radiocolumn :data="allreco.d2"></radiocolumn>
      <!-- 脱口秀 -->
      <radiorow :data="allreco.talkshow"></radiorow>
      <!-- 知识技能 -->
      <radiorow :data="allreco.knowledge"></radiorow>
      <!-- 商业财经 -->
      <radiorow :data="allreco.finance"></radiorow>
      <!-- 人文历史 -->
      <radiorow :data="allreco.history"></radiorow>
      <!-- 外语世界 -->
      <radiorow :data="allreco.foreignLanguage"></radiorow>
      <!-- 亲子宝贝 -->
      <radiorow :data="allreco.baby"></radiorow>
      <!-- 相声曲艺 -->
      <radiorow :data="allreco.crosstalk"></radiorow>
      <!-- 旅途|城市 -->
      <radiorow :data="allreco.city"></radiorow>
    </div>
    <load v-if="hasLoad"></load>
  </div>
</template>

<script>
import radiobanner from '../radio/radiobanner.vue'
import radiohear from '../radio/radiohear.vue'
import radiopay from '../radio/radiopay.vue'
import radiorow from '../radio/radiorow.vue'
import radiocolumn from '../radio/radiocolumn.vue'
import load from '../../components/load.vue'
export default {
  components: {
    radiobanner,
    radiohear,
    radiopay,
    radiorow,
    radiocolumn,
    load
  },
  data () {
    return {
      hasLoad: true,
      allreco: {
        // 创作
        creation: {},
        // 3d电子
        d3: {},
        // 广播剧
        radioplay: {},
        // 有声书
        audiobook: {},
        // 影视
        movies: {},
        // 美文读物
        lettres: {},
        // 脱口秀
        talkshow: {},
        // 知识技能
        knowledge: {},
        // 商业财经
        finance: {},
        // 人文历史
        history: {},
        // 外语世界
        foreignLanguage: {},
        // 亲子宝贝
        baby: {},
        // 相声曲艺
        crosstalk: {},
        // 路途|城市
        city: {},
        // 情感调频
        emotion: {},
        // 音乐故事
        story: {},
        // 二次元
        d2: {}
      }
    }
  },
  created () {
    this.fetchAllreco()
  },
  methods: {
    async fetchAllreco () {
      const res = await this.$http.get('dj/category/recommend')
      this.allreco.creation = res.data.data[0]
      this.allreco.d3 = res.data.data[1]
      this.allreco.emotion = res.data.data[2]
      this.allreco.story = res.data.data[3]
      this.allreco.d2 = res.data.data[4]
      this.allreco.audiobook = res.data.data[5]
      this.allreco.radioplay = res.data.data[6]
      this.allreco.lettres = res.data.data[7]
      this.allreco.crosstalk = res.data.data[8]
      this.allreco.history = res.data.data[9]
      this.allreco.talkshow = res.data.data[10]
      this.allreco.movies = res.data.data[11]
      this.allreco.foreignLanguage = res.data.data[12]
      this.allreco.knowledge = res.data.data[13]
      this.allreco.baby = res.data.data[14]
      this.allreco.finance = res.data.data[16]
      this.allreco.city = res.data.data[18]
      this.hasLoad = false
    }
  },
}
</script>

<style>
.radio_pages {
  margin-bottom: 1rem;
}
.radiohome_header {
  height: 1rem;
  padding: 0 0.2rem;
  display: flex;
  align-items: center;
  background-color: #fff;
}
.radiohome_header span {
  margin-left: 0.4rem;
  font-size: 0.32rem;
}
.grid_radio .van-grid-item__text {
  font-size: 0.24rem;
}
</style>


