<template>
  <div class="reco">
    <div class="">
      <span class="recosong">{{name}}</span>
      <div class="recoTitle">
        <span class="jx">
          {{title}}</span>
        <van-button round
                    size="small"
                    :to="router">
          <van-icon :name="icon"
                    size="0.3rem" />
          {{more}}</van-button>
      </div>
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    name: {},
    title: { required: true },
    more: {},
    icon: {},
    router: {}
  },
  methods: {

  },
  created () {
  },
  mounted () {
  },
}
</script>

<style>
.reco {
  vertical-align: middle;
}
.van-button__text {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.24rem;
}
.van-button__text i {
  margin-right: 0.1rem;
}
.van-button {
  height: auto;
  padding: 0.06rem 0.2rem;
}
</style>


