<template>
  <keep-alive>
    <div class="slide_pages">
      <div class="slideheader_card">
        <div class="slidecard">
          <div class="slidecard_title">
            <div class="slide_vip"
                 v-if="!userInfo">
              <p>开通黑胶VIP</p>
            </div>
            <div class="slide_vip"
                 v-if="userInfo">
              <img src="../assets/blackglum.png"
                   alt="">
            </div>
            <div class="slide_vipbtn">
              <button>
                会员中心
              </button>
            </div>
          </div>
          <!--  -->
          <div class="slidecard_content">
            <div class="slidecontent_info">
              <p>新用户首月仅5元</p>
              <p>还有限量5折年卡等你秒杀!</p>
            </div>
            <div class="slide_more">
              <van-icon name="arrow"
                        color="#797979"
                        size="0.4rem" />
            </div>
          </div>

        </div>
        <!-- 用户列表 -->
        <div class="slide_user">
          <div class="slide_flex1">
            <!-- 未登录情况 -->
            <div class="needlogin"
                 v-if="!userInfo">
              <div class="needlogin_button">
                <p>手机电脑多端同步,尽享海量高品质音乐</p>
                <button @click="layout">
                  立即登录
                </button>
              </div>
            </div>
            <div class="needlogin"
                 v-if="userInfo">
              <div class="slide_loginuser">
                <div class="slide_userleft">
                  <img :src="userInfo.avatarUrl"
                       alt="">
                  <span>{{userInfo.nickname}}</span>
                </div>
                <div class="slide_userright"
                     v-show="!$store.state.sing_in"
                     @click="singin()">
                  <button>签到</button>
                </div>
                <div class="slide_userright sign_in"
                     v-show="$store.state.sing_in">
                  <button ref="sing_in">已签到</button>
                </div>
              </div>
            </div>
            <!-- 登录之后 -->
            <!-- 按钮组 -->
            <div class="user_btnicon">
              <van-grid :border="false">
                <van-grid-item :badge='$store.state.newCountMsg'
                               icon="https://s1.ax1x.com/2020/04/22/JY7hnO.png"
                               text="我的消息" />
                <van-grid-item icon="https://s1.ax1x.com/2020/04/22/JY7oAH.png"
                               text="我的好友" />
                <van-grid-item icon="https://s1.ax1x.com/2020/04/22/JYHEuT.png"
                               text="个人主页" />
                <van-grid-item icon="https://s1.ax1x.com/2020/04/22/JYHmE4.png"
                               text="个性装扮" />
              </van-grid>
            </div>
            <van-divider />
            <!-- 列表组 -->
            <div class="slidelist_items">
              <div class="slidelist_item">
                <van-icon size="20"
                          name="https://s1.ax1x.com/2020/04/22/JYO5Y4.png" /><span>听歌识曲</span>
              </div>
              <div class="slidelist_item">
                <van-icon size="20"
                          name="https://s1.ax1x.com/2020/04/22/JYXvD0.png" /><span>演出</span>
              </div>
              <div class="slidelist_item">
                <van-icon size="20"
                          name="https://s1.ax1x.com/2020/04/22/JYjEK1.png" /><span>商城</span>
              </div>
              <div class="slidelist_item">
                <van-icon size="20"
                          name="https://s1.ax1x.com/2020/04/22/JtpsEt.png" /><span>附近的人</span>
              </div>
              <van-divider />
              <div class="slidelist_item">
                <van-icon size="20"
                          name="https://s1.ax1x.com/2020/04/22/JtpgC8.png" /><span>定时停止播放</span>
              </div>
              <div class="slidelist_item">
                <van-icon size="20"
                          name="https://s1.ax1x.com/2020/04/22/JtpRgg.th.png" /><span>扫一扫</span>
              </div>
              <div class="slidelist_item">
                <van-icon size="20"
                          name="https://s1.ax1x.com/2020/04/22/JtpL2F.th.png" /><span>音乐闹钟</span>
              </div>
              <div class="slidelist_item">
                <van-icon size="20"
                          name="https://s1.ax1x.com/2020/04/22/Jt98MQ.th.png" /><span>在线听歌免流量</span>
              </div>
              <div class="slidelist_item">
                <van-icon size="20"
                          name="https://s1.ax1x.com/2020/04/22/Jt9UI0.th.png" /><span>青少年模式</span>
              </div>
            </div>
          </div>
          <!-- <van-divider /> -->
          <!-- 底部退出导航 -->
          <div class="slide_setting">
            <div class="slide_set slide_night">
              <van-icon size="20"
                        :name="nightIcon" /><span>{{night}}</span>
            </div>
            <div class="slide_set slide_sett">
              <van-icon size="20"
                        name="https://s1.ax1x.com/2020/04/22/JYLpUe.png" /><span>设置</span>
            </div>
            <div class="slide_set slide_loyout"
                 @click="layout">
              <van-icon size="20"
                        name="https://s1.ax1x.com/2020/04/22/JYbYQ0.png" /><span>切换账号</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </keep-alive>

</template>

<script>
export default {
  data () {
    return {
      userInfo: JSON.parse(localStorage.getItem('userInfo')) || '',
      night: '夜间模式',
      nightIcon: 'https://s1.ax1x.com/2020/04/22/JYOeQ1.png'
    }
  },
  methods: {
    layout () {
      // console.log(111);
      // this.$nextTick(() => {
      localStorage.removeItem('userInfo')
      localStorage.removeItem('level')
      this.$router.push('/loginTip')
      this.$store.commit('loyout')
      // window.clearInterval(this.timeInterval)
      // })
    },
    // 签到
    async singin () {
      this.$store.commit('singin')
      this.$toast('签到成功')
    }
  },
}
</script>

<style>
.slide_pages {
  height: 100vh;
  position: relative;
  box-sizing: border-box;

  background-image: linear-gradient(to right, #778ca3, #778ca3);
}
.slideheader_card {
  box-sizing: border-box;

  position: relative;
  height: 20vh;
  background-image: linear-gradient(to right, #778ca3, #778ca3);
}
.slidecard {
  padding: 0.2rem 0;
  box-sizing: border-box;

  position: absolute;
  width: 80%;
  display: flex;
  flex-wrap: wrap;
  height: 80%;
  left: 50%;
  transform: translate(-50%);
  bottom: 0;
  /* border: 1px solid red; */
  border-radius: 0.2rem 0.2rem 0 0;
  box-shadow: -0.06rem -0.1rem 0.2rem #576574;
}
.slidecard_title {
  box-sizing: border-box;
  width: 100%;
  display: flex;
  height: 30%;
  padding: 0 0.2rem;
  align-items: center;
  margin-bottom: 0.4rem;
  justify-content: space-between;
}
.slide_vip p {
  font-size: 0.36rem;
  color: #ffda79;
  margin: 0;
}

.slide_vip img {
  width: 0.8rem;
}
.slide_vipbtn button {
  border: none;
  outline: none;
  border-radius: 0.2rem;
  font-size: 0.24rem;
  color: #666;
  background-image: linear-gradient(to right, #ffda79, #cc8e35);
  padding: 0.06rem 0.2rem;
}
.slidecard_content {
  box-sizing: border-box;
  width: 100%;
  position: absolute;

  bottom: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 0.2rem;
  height: 40%;
  background-color: rgba(255, 255, 255, 0.2);
}
.slidecontent_info p {
  margin: 0;
  font-size: 0.24rem;
}
.slidecontent_info p:nth-child(1) {
  color: #ffda79;
}
.slidecontent_info p:nth-child(2) {
  color: #eee;
}
/*  */
.slide_user {
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 0.3rem 0.3rem 0 0;
  width: 100%;
  position: absolute;
  display: flex;
  flex-direction: column;
  top: 20vh;
  height: 85vh;
}
.needlogin {
  margin-top: 0.2rem;
}
.slide_more {
  height: 100%;
  display: flex;
  align-items: center;
}
.needlogin_button {
  box-sizing: border-box;
  text-align: center;
  padding: 0 0.2rem 0.2rem 0.2rem;
}
.needlogin_button p {
  font-size: 0.24rem;
}
.needlogin_button button {
  background-color: #e84118;
  border: none;
  outline: none;
  font-size: 0.24rem;
  color: #fff;
  padding: 0.16rem 0.6rem;
  border-radius: 0.4rem;
  /* letter-spacing: 0.1rem; */
}
.user_btnicon {
  box-sizing: border-box;
  padding: 0.2rem 0.2rem 0 0.2rem;
}
.user_btnicon .van-grid-item__text {
  font-size: 0.24rem;
}
/* 列表组 */
.slidelist_item {
  display: flex;
  align-items: center;
  padding: 0.2rem;
}
.slidelist_item:active {
  background-color: #eee;
}
.slidelist_item span {
  margin-left: 0.3rem;
  font-size: 0.28rem;
}
/* 底部导航 */
.slide_setting {
  box-sizing: border-box;
  height: 1rem;
  display: flex;
  align-items: center;
  width: 100%;
  background-color: #fff;
  border-top: 0.02rem solid #ebedf0;
  margin-top: 0.4rem;
  padding: 0.2rem 0;
}
.slide_set {
  display: flex;
  align-items: center;
  height: 100%;
  justify-content: center;
}
.slide_set span {
  margin-left: 0.2rem;
  font-size: 0.28rem;
}
.slide_night {
  width: 35%;
}
.slide_sett {
  width: 30%;
}
.slide_loyout {
  width: 30%;
}
/*  */
.slide_loginuser {
  display: flex;
  padding: 0 0.4rem;
  margin-top: 0.4rem;
  align-items: center;
  justify-content: space-between;
}
.slide_userleft {
  display: flex;
  align-items: center;
}
.slide_userleft span {
  margin-left: 0.2rem;
  font-size: 0.32rem;
}
.slide_loginuser img {
  width: 0.8rem;
  border-radius: 50%;
}
.slide_userright {
  display: flex;
  align-items: center;
}
.slide_userright button {
  outline: none;
  border: none;
  width: 1.6rem;
  padding: 0.1rem 0.2rem;
  font-size: 0.24rem;
  background-color: #e84118;
  color: #fff;
  border-radius: 0.4rem;
}
.sign_in button {
  background-color: #797979;
}
.slide_flex1 {
  flex: 1;
}
</style>


