<template>
  <div>
    <div class="radiorow_main">
      <div class="radiorow_header">
        <span>{{data.categoryName}}</span>
        <van-icon name="arrow"
                  size="0.4rem" />
      </div>
      <!-- 数据列表 -->
      <div class="radiorow_items">
        <div class="radiorow_item"
             v-for="(item, index) in data.radios"
             :key="index">
          <div class="radiorow_img">
            <img v-lazy="item.picUrl"
                 alt="">
            <div class="radiorow_desc">
              {{item.rcmdText}}
            </div>
          </div>
          <p>{{item.name}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    data: {
      type: Object
    },
    title: {
      type: String
    }
  }
}
</script>

<style>
.radiorow_main {
  padding: 0 0.1rem;
}
.radiorow_header {
  display: flex;
  padding: 0.2rem 0.1rem;
  align-items: center;
}
.radiorow_header span {
  font-size: 0.32rem;
}
.radiorow_header .van-icon {
  display: flex;
  align-items: center;
}
.radiorow_items {
  display: flex;
}
.radiorow_item {
  display: flex;
  flex-direction: column;
  width: 33.3%;
  padding: 0.2rem 0.1rem;
}
/* .radiorow_img::before {
  content: "";
  position: absolute;
  width: 100%;
  background-color: rgba(42, 42, 42, 0.69);
  opacity: 0.5;
  height: 96.5%;
  border-radius: 0.1rem;
  left: 0;
  top: 0;
  bottom: 0;
} */
.radiorow_img {
  position: relative;
  flex: 1;
}
.radiorow_img img {
  border-radius: 0.1rem;
  background-image: rgba(42, 42, 42, 0.69);
  width: 100%;
}
.radiorow_item p {
  font-size: 0.24rem;
  margin: 0;
  height: 0.64rem;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}
.radiorow_desc {
  font-size: 0.2rem;
  color: #ffffff;
  position: absolute;
  bottom: 0.26rem;
  padding: 0.06rem;
  border-radius: 0 0 0.1rem 0.1rem;
  left: 0;
  background-color: rgba(50, 50, 50, 0.25);
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>


