<template>
  <div>
    <van-divider />
    <div class="radio_hear">
      <div class="radio_title">
        <p>
          电台推荐
        </p>
        <button @click="fetchDjRecomm">
          <van-icon name="replay" />
          换一换</button>
      </div>
      <div class="radiohear_content">
        <div class="radiohear_item"
             v-for="(item, index) in djrecommend"
             :key="index">
          <div class="radiohear_itemtitle">
            <div class="radiohear-itemimg">
              <img v-lazy="item.picUrl"
                   alt="">
            </div>
            <div class="radiohear_imgalt">
              {{item.name}}
            </div>
          </div>
          <p>{{item.rcmdtext}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      djrecommend: []
    }
  },
  created () {
    this.fetchDjRecomm()
  },
  methods: {
    async fetchDjRecomm () {
      this.djrecommend = []
      const res = await this.$http.get('dj/recommend')
      let recoArr = []
      for (let i = 0; i < 3; i++)      {
        let num = Math.floor(Math.random() * 10)
        if (recoArr.indexOf(num) == -1)        {
          recoArr.push(num)
          this.djrecommend.push(res.data.djRadios[num])
        }
      }
    },
  },
}
</script>

<style>
.radio_title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.2rem;
}
.radio_title p {
  margin: 0;
  font-size: 0.32rem;
}
.radio_title button {
  border: none;
  border: 0.02rem solid #ddd;
  outline: none;
  border-radius: 0.4rem;
  background-color: #fff;
  padding: 0.06rem 0.2rem;
  font-size: 0.24rem;
  display: flex;
  align-items: center;
  /* letter-spacing: 0.1rem; */
}
.radiohear_content {
  display: flex;
  padding: 0 0.1rem;
}
.radiohear_item {
  padding: 0.2rem 0.1rem;
  width: 33.3%;
}
.radiohear_itemtitle {
  position: relative;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.radiohear_imgalt {
  position: absolute;
  bottom: 0.3rem;
  left: 0.1rem;
  font-size: 0.24rem;
  width: 100%;
  color: #fff;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.radiohear-itemimg {
  height: 100%;
}
.radiohear_itemtitle img {
  width: 100%;
  object-fit: cover;
  height: 100%;
  border-radius: 0.1rem;
}
/* .radiohear-itemimg::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 96.8%;
  border-radius: 0.1rem;
  background-color: rgba(42, 42, 42, 0.69);
  opacity: 0.5;
  left: 0;
  top: 0;
  bottom: 0;
} */
.radiohear_item p {
  font-size: 0.24rem;
  height: 0.8rem;
  margin: 0;
  margin-top: 0.1rem;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>


