<template>
  <keep-alive>
    <div class="mylovesong_pages">
      <div v-if="!loading">
        <div class="mylove_header">
          <div class="mylove_headerleft">
            <van-icon name="arrow-left"
                      size="0.5rem"
                      color="#fff"
                      @click="$router.go(-1)" /><span>
              歌单
            </span>
          </div>
          <div class="mylove_headerright">
            <van-icon name="https://s1.ax1x.com/2020/03/28/GFyZG9.png"
                      size="0.4rem"
                      ref="searchIcon"
                      @click="searchByName()" />
            <input type="text"
                   v-model="searchValue"
                   v-show="searchLoad"
                   @change="filterSong">
            <van-icon name="https://s1.ax1x.com/2020/03/28/GFsgHO.png"
                      size="0.6rem" />
          </div>

        </div>
        <!-- 背景图片 -->
        <div class="mylove_bcg">
          <div class="mylove_info">
            <div class="mylove_infoimg">
              <div class="mylove_info_img">
                <img :src="songs[0].al.picUrl"
                     alt="">
                <!-- 遮罩层 -->
                <div class="mylove_img_droper">

                </div>
                <!-- 爱心 -->
                <div class="mylove_img_droperimg">
                  <img src="https://s1.ax1x.com/2020/04/23/JaqjVP.png"
                       alt="">
                </div>
              </div>
            </div>
            <div class="mylove_inforight">
              <h3>我喜欢的音乐</h3>
              <div class="mylove_userinfo">
                <img :src="userInfo.avatarUrl"
                     alt=""><span>{{userInfo.nickname}}</span>
                <van-icon name="arrow"
                          color="#fff"
                          size="0.5rem" />
              </div>
            </div>
          </div>
          <div class="grid_info">
            <van-grid :column-num="4"
                      :border="false">
              <van-grid-item icon="https://s1.ax1x.com/2020/03/28/GFsRED.png"
                             text="评论" />
              <van-grid-item icon="https://s1.ax1x.com/2020/03/28/GFs58A.png"
                             text="分享" />
              <van-grid-item icon="https://s1.ax1x.com/2020/03/28/GFsf4H.png"
                             text="下载" />
              <van-grid-item icon="https://s1.ax1x.com/2020/03/28/GFsovt.png"
                             text="多选" />
            </van-grid>
          </div>
        </div>
        <!-- 列表 -->
        <div class="mylove_items">
          <div class="mylove_itemstitle"
               @click="playAll()">
            <van-icon name="https://s1.ax1x.com/2020/03/26/GphbSs.png"
                      size="0.4rem"></van-icon>
            <h5>播放全部</h5><span>(共{{songs.length}}首)</span>
          </div>
          <div class="mylove_item"
               @click="handleSong(item,index,item.id)"
               v-for="(item, index) in filterSong()"
               :key="index">
            <div class="mylove_itemindex">
              {{index+1}}
            </div>
            <div class="mylove_itemright">
              <div class="mylove_itemright_left"
                   style="line-height:20px">
                <div class="mylove_left_name suojin">{{item.name}}</div>
                <div class="mylove_left_author suojin">
                  <span class="songlist_sq"
                        v-if="item.fee==1"
                        :class="{'sprite_vip':item.fee==1,}"></span>
                  <span class="songlist_sq"
                        :class="{'sprite3': item.fee==4 ||item.copyright==2 ||item.copyright==0 ||item.fee==1}"
                        v-if="item.fee==4 ||item.copyright==2 ||item.copyright==0 ||item.fee==1"></span>
                  {{item.ar.map(v=>v.name).join('/')}} - {{item.al.name}}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <load v-if="loading"></load>
    </div>
  </keep-alive>

</template>

<script>
import load from '../components/load.vue'
export default {
  components: {
    load
  },
  data () {
    return {
      userInfo: JSON.parse(localStorage.getItem('userInfo')) || '',
      // 歌曲id列表
      ids: [],
      songurl: '',
      // 所有歌曲详细数据
      songs: [],
      searchValue: '',
      searchLoad: false,
      loading: true
    }
  },
  created () {
    this.fetchLoveSong(this.userInfo.userId)
  },
  methods: {
    handleSong (item, index, songid, singer, songname) {
      // console.log(item);
      if (item.fee == 1)      {
        return this.$toast('vip歌曲暂时无法播放')
      } else if (item.fee == 4)      {
        return this.$toast('请先购买专辑')
      }
      // 判断歌曲是否有版权
      this.$http.get(`/check/music?id=${item.id}`).then(res => {
        this.$store.commit('changeFull')
        this.$store.commit("addSong", { item: item.al, index: index, songid: songid, singer: item.ar.map(v => v.name).join('/'), songname: item.name })
        this.$store.commit('pushSong', item.al)
        this.fetchSongurl(item.id)
      }, err => {
        this.hasCopy = false
        return this.$toast('亲爱的,暂无版权')
      })
    },
    async  fetchSongurl (id) {
      const res = await this.$http.get(`song/url?id=${id}`)
      this.songurl = res.data.data[0].url
      this.$store.commit('addurl', this.songurl)
    },
    con (a, b) {
      return a - b;
    },
    // 点击显示搜索框
    searchByName () {
      this.$refs.searchIcon.style.display = 'none'
      this.searchLoad = true
    },
    // 根据用户id获取所有歌曲id
    async fetchLoveSong (id) {
      const res = await this.$http.get(`likelist?uid=${id}`)
      this.ids = res.data.ids
      this.fetchLoveAllSong()
    },
    // 拿到所有ids之后循环请求详细歌曲数据
    async fetchLoveAllSong () {
      const result = await this.$http.get(`song/detail?ids=${this.ids}`)
      this.songs = result.data.songs
      this.loading = false
    },
    // 索引歌曲
    filterSong () {
      return this.songs.filter(item => item.name.includes(this.searchValue))
    },
    // 播放全部
    playAll () {
      this.songs.map(async v => {
        const res = await this.$http(`song/url?id=${v.id}`)
        v.al.songname = v.name
        v.al.songid = v.id
        v.al.singer = v.ar.map(a => a.name).join('/')
        v.al.url = res.data.data[0].url
      })
      this.$store.commit('pushAllSong', this.songs)
    }
  },
}
</script>

<style>
.mylovesong_pages {
  height: 100vh;
  position: relative;
}
.mylove_header {
  height: 1.2rem;
  display: flex;
  position: relative;
  z-index: 2;
  justify-content: space-between;
  padding: 0.2rem;
  font-size: 0.32rem;
}
.mylove_headerleft {
  display: flex;
  align-items: center;
}
.mylove_headerleft span {
  margin-left: 0.2rem;
  color: #fff;
}
.mylove_headerright {
  display: flex;
  align-items: center;
}
.mylove_headerright .van-icon {
  margin-left: 0.4rem;
}
.mylove_bcg {
  background-image: url(https://s1.ax1x.com/2020/04/23/Ja2GLQ.jpg);
  background-size: cover;
  background-position: center;
  height: 7rem;
  width: 100%;
  position: relative;
  margin-top: -1.2rem;
  z-index: -1;
}
.mylove_bcg::before {
  position: absolute;
  width: 100%;
  height: 100%;
  content: "";
  background-color: rgba(42, 42, 42, 0.69);
  opacity: 0.8;
  left: 0;
  top: 0;
  bottom: 0;
}
.grid_info {
  position: absolute;
  bottom: 0.4rem;
  width: 100%;
}
.grid_info .van-grid-item__content {
  background-color: transparent;
}
.grid_info .van-grid-item__text {
  color: #fff;
}
/*  */
.mylove_items {
  position: relative;
  margin-top: -0.4rem;
  background-color: #fff;
  border-radius: 0.3rem 0.3rem 0 0;
  margin-bottom: 1rem;
}
.mylove_itemstitle {
  padding: 0 0.4rem;
  height: 1rem;
  display: flex;
  align-items: center;
}
.mylove_itemstitle h5 {
  margin: 0;
  margin-left: 10px;
  font-size: 0.3rem;
  font-weight: 500;
}
.mylove_itemstitle span {
  color: #797979;
  font-size: 0.24rem;
}
.mylove_item {
  display: flex;
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
}
.mylove_item:active {
  background-color: #eee;
}
.mylove_itemindex {
  font-style: italic;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #797979;
  width: 1.1rem;
  height: 1.1rem;
  font-size: 0.32rem;
}
.mylove_itemright {
  flex: 1;
  overflow: hidden;
}
.suojin {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.mylove_left_name {
  width: 70%;
  font-size: 0.28rem;
  box-sizing: border-box;
  height: 0.36rem;
}
.mylove_left_author {
  width: 70%;
  font-size: 0.24rem;
  color: #797979;
  height: 0.36rem;
}
.mylove_headerright input[type="text"] {
  border-radius: 0.4rem;
  border: none;
  outline: none;
  padding: 0.1rem 0.3rem;
  background-color: rgba(0, 0, 0, 0.5);
  font-size: 0.24rem;
  color: #fff;
}
.sprite_vip {
  background: url(https://s1.ax1x.com/2020/04/11/GbXi9K.png) no-repeat 0 0;
  background-size: 0.86rem 11.08rem;
  width: 0.12rem;
  height: 0.12rem;
}
.songlist_sq {
  display: inline-block;
}
.sprite3 {
  background: url("//s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28…=")
    no-repeat 0 0;
  background-size: 3.32rem 1.94rem;
  width: 0.24rem;
  height: 0.16rem;
}
.mylove_info {
  padding: 1.6rem 0.4rem 0.2rem 0.4rem;
  position: relative;
  display: flex;
  justify-content: space-between;
  z-index: 2;
}
.mylove_infoimg {
  width: 3rem;
}
.mylove_infoimg img {
  width: 100%;
  border-radius: 0.1rem;
  height: 100%;
}
.mylove_inforight {
  margin-left: 0.4rem;
  flex: 1;
  display: flex;
  flex-direction: column;
}
.mylove_inforight h3 {
  margin: 0;
  font-size: 0.32rem;
  color: #fff;
}
.mylove_userinfo {
  margin-top: 0.2rem;
  display: flex;
  align-items: center;
}
.mylove_userinfo img {
  width: 0.6rem;
  border-radius: 50%;
}
.mylove_userinfo span {
  font-size: 0.24rem;
  color: #fff;
  margin-left: 0.2rem;
}
.mylove_info_img {
  position: relative;
  width: 100%;
  height: 100%;
}

.mylove_img_droper {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  border-radius: 0.1rem;
  background-color: rgba(42, 42, 42, 0.69);
  opacity: 0.6;
  z-index: 2;
}
.mylove_itemright_left {
  line-height: 0.8rem;
}
.mylove_img_droperimg {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  z-index: 3;
  display: flex;
  align-items: center;
  justify-content: center;
}
.mylove_img_droperimg img {
  width: 60%;
  height: 50%;
}
</style>


