<template>
  <div>
    <div class="banner_radio">
      <van-swipe :autoplay="5000"
                 :loop="true">
        <van-swipe-item v-for="(image, index) in radiobanner"
                        :key="index">
          <van-tag class="tag_title_radio">{{image.typeTitle}}</van-tag>
          <a :href="image.url"><img v-lazy="image.pic" /></a>
        </van-swipe-item>
      </van-swipe>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      radiobanner: []
    }
  },
  created () {
    this.fetchRadioBanner()
  },
  methods: {
    async fetchRadioBanner () {
      const res = await this.$http.get('dj/banner')
      this.radiobanner = res.data.data
    }
  },
}
</script>

<style>
.banner_radio {
  border-radius: 0.2rem;
  margin: 0.2rem auto;
  width: 90%;
}
.tag_title_radio {
  position: absolute;
  background-color: #dd101b;
  color: #fff;
  right: 0px;
  bottom: 0.26rem;
  border-radius: 0.2rem 0 0.2rem 0;
  padding: 0.1rem 0.16rem;
  font-size: 0.24rem;
}
.banner_radio img {
  width: 100%;
  border-radius: 0.2rem;
}
.van-swipe {
  height: 100%;
  border-radius: 0.2rem;
}
</style>


