<template>
  <div>
    <div class="radiocolumn_main">
      <div class="radiocolumn_header">
        <div class="radiocolumn_headerleft">
          <span>{{data.categoryName}}</span>
          <van-icon name="arrow"
                    size="0.4rem" />
        </div>
        <div class="radiocolumn_playall">
          <button>
            <van-icon name="play-circle" /><span>播放全部</span></button>
        </div>
      </div>
      <!--  -->
      <div class="radiocolumn_items">
        <div class="radiocolumn_item"
             v-for="(item, index) in data.radios"
             :key="index">
          <div class="radiocolumn_itemleft">
            <img v-lazy="item.picUrl"
                 alt="">
          </div>
          <div class="radiocolumn_itemright">
            <div class="radiocolumn_itemright_left">
              <div class="radiocolum_itemrecotext">
                {{item.lastProgramName}}
              </div>
              <div class="radiocolum_itemreconame">
                <span>{{item.name}}</span>&nbsp;&nbsp;&nbsp;<span>|&nbsp;&nbsp;&nbsp;
                  <van-icon name="fire" />{{item.programCount}}万</span>
              </div>
            </div>
            <div class="radiocolumn_itemright_right">
              <van-icon name="https://s1.ax1x.com/2020/03/26/GphbSs.png"
                        size="0.4rem" />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    data: {
      type: Object
    }
  }
}
</script>

<style>
.radiocolumn_header {
  padding: 0 0.2rem;
  display: flex;
  height: 1rem;
  box-sizing: border-box;

  justify-content: space-between;
  align-items: center;
}
.radiocolumn_headerleft {
  box-sizing: border-box;
  display: flex;
  align-items: center;
}
.radiocolumn_headerleft span {
  font-size: 0.32rem;
}
.radiocolumn_playall button {
  display: flex;
  align-items: center;
  outline: none;
  border: 0.02rem solid #ddd;
  background-color: #fff;
  border-radius: 0.4rem;
  font-size: 0.2rem;
  padding: 0.04rem 0.2rem;
}
.radiocolumn_playall span {
  margin-left: 0.1rem;
}
.radiocolumn_items {
  padding: 0.1rem 0px;
}
.radiocolumn_item:active {
  background-color: #eee;
}
.radiocolumn_item {
  padding: 0.1rem 0.2rem;
  display: flex;
  align-items: center;
  box-sizing: border-box;
}
.radiocolumn_itemright {
  display: flex;
  align-items: center;
  margin-left: 0.2rem;
  justify-content: space-between;
  box-sizing: border-box;
  flex: 1;
  overflow: hidden;
  /* width: 70%; */
}
.radiocolumn_itemright_left {
  box-sizing: border-box;
  /* flex: 1; */
  width: 80%;
}
.radiocolumn_itemleft {
  display: flex;
  align-items: center;
}
.radiocolumn_itemleft img {
  width: 1.2rem;
  border-radius: 0.1rem;
}
.radiocolum_itemrecotext {
  box-sizing: border-box;
  font-size: 0.24rem;
  padding: 0.1rem 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 90%;
}
.radiocolum_itemreconame {
  box-sizing: border-box;
  font-size: 0.24rem;
  display: flex;
  width: 100%;
  align-items: center;
}

.radiocolum_itemreconame span:nth-child(1) {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: block;
  max-width: 60%;
}
.radiocolum_itemreconame span:nth-child(2) {
  color: #999;
  display: flex;
  align-items: center;
  max-width: 40%;
}
.radiocolumn_itemright_right {
  width: 20%;
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>


